@import "../../global.scss";

.topbar {
  width: 100%;
  height: 70px;
  background-color: $mainColor;
  color: $lightColor;
  position: fixed;
  top: 0;
  z-index: 3;
  transition: all 1s ease;

  .wrapper {
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .left {
      display: flex;
      align-items: center;

      .name {
        font-size: 30px;
        font-weight: 700;
        text-decoration: none;
        color: inherit;
        margin-right: 40px;
      }
    }

    .right {
      display: flex;
      align-items: center;

      .sectionLink {
        display: flex;
        align-items: center;
        margin-left: 30px;

        a {
          color: inherit;
          font-size: 15px;
          font-weight: 500;

          // text-decoration: underline;
        }
      }
    }
  }

