$mainColor: rgb(0, 0, 0);
$lightColor: rgb(250, 250, 250);
$accentColor: rgb(194, 45, 0);

$topbarHeight: 70px;
$width: 768px;

@mixin mobile {
  @media (max-width : #{$width}) {
    @content;
  }
}

.sectionTitle {
  font-size: 40px;
}

.lineContainer {
  // background-color: pink;

  align-items: stretch;
  align-items: center;
  justify-content: center;
  display: flex;

  .hLine {
    width: 80%;
    height: 3px;

    background-color: $mainColor;
    border-radius: 100px;
  }

  .vLine {
    margin-left: 8px;
    margin-right: 8px;

    width: 2px;
    height: 80%;

    background-color: $mainColor;
    border-radius: 100px;
  }
}
